/*公卫服务公共样式开始*/

.healthservice {
	font-size: 0.28rem;
	width:calc(100% - 200px);
	margin-left: 200px;
	height: 100%;
}

/*左侧蓝色线*/

.healthservice .lineleft {
	display: inline-block;
	width: 0.04rem;
	height: 0.3rem;
	background: #3C9BFF;
}

/*通用input*/

.healthservice input {
	width: 19vw;
	height: 0.6rem;
}

/*左边导航*/

.healthservice .left{
	width: 16.8%;
}

/*导航具体项目*/

.healthservice .left .item{
	height: 0.9rem;
	line-height: 0.9rem;
	width: 100%;
}

/*大input*/

.healthservice input.big {
	width: 58vw
}

/*小input*/

.healthservice input.small {
	width: 15vw
}

/*选择按钮*/

.healthservice .select{
	display: inline-block;
	width: 2.3rem;
	line-height: 0.55rem;
	font-size: 0.23rem;
	border-radius: 7px;
	color: #fff;
	background: rgba(60, 155, 255, 1);
	text-align: center;
}

/*选择小按钮未选中*/

.healthservice .chosebtn{
	display: inline-block;
    margin-left: 15px;
    border: 1px solid #3C9BFF;
    line-height: .6rem;
    padding: 0 0.4rem;
    border-radius: 8px;
    color: #3C9BFF;
}

/*选择小按钮选中*/

.healthservice .chosebtn.on{
	display: inline-block;
    margin-left: 15px;
    background:#3C9BFF;
    border: 1px solid #3C9BFF;
    line-height: .6rem;
    padding: 0 0.4rem;
    border-radius: 8px;
    color: #fff;
}

/*检测按钮*/

.healthservice .detect {
	display: inline-block;
	line-height: 0.6rem;
	border: 1px solid #3C9BFF;
	padding: 0 0.35rem;
	font-size: 0.26rem;
	border-radius: 0.1rem;
	color: #3C9BFF;
}

/*选择框未选中*/

.healthservice .selectbox{
	width:0.5rem;
	height:0.5rem;
	border: 1px solid #999999;
	background: #FFFFFF;
	border-radius: 0.3rem;
}

/*选择框选中*/

.healthservice .selectbox.on{
	background: url(data:image/png;base64,);
	background-size: contain;
	border: none;
}

/*公卫服务公共样式结束*/

